<template>
	<div id="deliveryCard-order">
		<public-header title="购买记录"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">未到期</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">已到期</div>
				</div>
				<van-search placeholder="请输入用户姓名或UID" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="order-list"
			>
				<div class="order-item" v-for="(item, index) in records.data" :key="item.id">
					<div class="order-title flex-lr">
						<div class="title flex">
							<div class="title-img">
								<img :src="item.avatar" alt="">
							</div>
							<div class="title-name">{{item.realname}}</div>
							<div class="gift-type itag itag-primary">{{item.card_name}}</div>
						</div>
						<div class="itag" :class="{'itag-primary': item.card_status == '生效中', 'itag-danger': item.card_status == '已到期'}">{{item.card_status}}</div>
					</div>
					<div class="order-detail border-1px-t">
						<div class="order-info flex">
							<div>购买费用：</div>
							<div class="c-danger">￥{{item.final_fee}}</div>
						</div>
						<div class="order-info flex">
							<div>购买时间：</div>
							<div>{{item.paytime_cn}}</div>
						</div>
						<div class="order-info flex">
							<div>支付方式：</div>
							<div>{{item.pay_type_cn}}</div>
						</div>
						<div class="order-info flex">
							<div>有效期：</div>
							<div>{{item.starttime_cn}} ~ {{item.endtime_cn}}</div>
						</div>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {
						status: '-1'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/deliveryCard/order/order'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#deliveryCard-order .content .order-item{
		margin-bottom: 10px;
		background-color: #fff;
	}
	#deliveryCard-order .content .order-item .order-title{
		padding: 10px;
	}
	#deliveryCard-order .content .order-item .order-title .title-img{
		width: 35px;
		height: 35px;
	}
	#deliveryCard-order .content .order-item .order-title .title-img img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#deliveryCard-order .content .order-item .order-title .title-name{
		font-size: 16px;
		font-weight: bold;
		padding: 0 10px;
	}
	#deliveryCard-order .content .order-item .order-title .gift-type{
		font-size: 12px;
		padding: 2px 4px;
	}
	#deliveryCard-order .content .order-item .order-detail{
		padding: 10px;
	}
	#deliveryCard-order .content .order-item .order-detail .order-info{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 8px;
	}
	#deliveryCard-order .content .order-item .order-detail .order-info:last-child{
		padding-bottom: 0px;
	}
</style>


